@import "~antd/dist/antd.css";

.App > header {
  color: #fff;
  text-transform: uppercase;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}

.App > header p {
  line-height: 25px;
  margin-bottom: 0;
}

.App > header p.title {
  margin-top: 6px;
}

.App > header p.sub-title {
  font-size: 0.8em;
  color: #cccccc;
}

.App table tr.drop-over-downward td {
  border-bottom: 2px dashed #1890ff;
}

.App table tr.drop-over-upward td {
  border-top: 2px dashed #1890ff;
}

@media (max-width: 575px) {
  .ant-form .ant-form-item .ant-form-item-label,
  .ant-form .ant-form-item .ant-form-item-control {
    flex: auto;
  }
  .ant-form .ant-form-item {
    margin-bottom: 10px;
  }
}
